<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <!-- build button for edit section  -->
      <v-btn
        class="ma-2"
        outlined
        small
        fab
        v-bind="attrs"
        v-on="on"
        color="primary"
      >
        <v-icon>mdi-pencil-outline</v-icon>
      </v-btn>
      <!-- end  build button for add section  -->
    </template>
    <v-form ref="form" lazy-validation v-model="valid">
      <v-card>
        <!-- title card dialog  -->
        <v-card-title>
          <span class="text-h5">Edit section</span>
        </v-card-title>
        <!--  -->
        <v-card-text>
          <v-container>
            <v-row>
              <!-- Name field card  -->
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Section Name Ar *"
                  required
                  v-model="section.name.ar"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Section Name En*"
                  required
                  v-model="section.name.en"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  :rules="nameRules"
                  label="Section Name Tr *"
                  required
                  v-model="section.name.tr"
                ></v-text-field>
              </v-col>
              <!--  -->

              <!-- Description field card  -->

              <v-col cols="12">
                <v-textarea
                  label="Section Description Ar*"
                  v-model="section.description.ar"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  label="Section Description En*"
                  v-model="section.description.en"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  label="Section Description Tr*"
                  v-model="section.description.tr"
                  :rules="descriptionRules"
                >
                </v-textarea>
              </v-col>
              <!--  -->
              <!-- build areas field  -->
              <v-col cols="12">
                <v-autocomplete
                  :rules="areasRules"
                  v-model="sectionAreas"
                  label="Implementation areas"
                  multiple
                  :items="allAreas"
                  item-text="name"
                  item-value="id"
                >
                  <template v-slot:selection="data">
                    <v-chip
                      class="primary"
                      v-bind="data.attrs"
                      :input-value="data.name"
                      close
                      @click="data.select"
                      @click:close="remove(data.item)"
                    >
                      {{ data.item.name }}
                    </v-chip>
                  </template>
                </v-autocomplete>
              </v-col>
              <!-- end build areas field  -->

              <!-- build nation field  -->
              <build-nation-field
                v-on:changNation="changNation"
                :nation="section.nation_id"
              />
              <!-- end build nation field  -->

              <!-- build image field  -->
              <v-col cols="12">
                <v-file-input
                  v-model="file"
                  chips
                  accept="image/png, image/jpeg"
                  label="Image"
                  @change="onFileChange"
              /></v-col>
              <v-col cols="12">
                <v-img
                  contain
                  max-height="250"
                  aspect-ratio="1"
                  :lazy-src="image ? image : minUrlImage + section.image_url"
                  :src="image ? image : minUrlImage + section.image_url"
                >
                </v-img>
              </v-col>
            </v-row>
          </v-container>
          <small color="red">*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="cancel"> Cancel </v-btn>
          <v-btn
            color="secondary"
            class="add-section-button"
            text
            @click="updateSection"
          >
            <span v-if="!progressEditSection">Update Section</span>
            <v-progress-circular
              v-if="progressEditSection"
              color="primary"
              indeterminate
            ></v-progress-circular>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-form>
  </v-dialog>
</template>
<script>
import { sectionController } from "./section";
export default {
  props: ["sectionData"],
  data() {
    return {
      // form
      valid: true,
      // validation form
      nameRules: [(v) => !!v || "Name is required"],
      descriptionRules: [(v) => !!v || "Description is required"],
      areasRules: [(v) => !!(v && v.length) || "Choose at least section"],

      // image
      file: null,
      image: null,
      section: {
        name: {},
        description: {},
      },
      minUrlImage: this.$store.state.imageUlr + "section/",
      //
      progressEditSection: false,
      isUpdating: false,
      dialog: false,
      allAreas: [],
      sectionAreas: [],
      //  selectedAreas contains the id of areas from send this ids to server ..
    };
  },

  watch: {
    isUpdating(val) {
      if (val) {
        setTimeout(() => (this.isUpdating = false), 3000);
      }
    },
    dialog(stat) {
      if (stat) {
        this.section = this.sectionData;
        this.getAreas();
      }
    },
  },

  methods: {
    getAreas: async function () {
      this.allAreas = this.$store.getters.areas;
      const result = this.allAreas.map((item) => {
        return {
          name: item.name[this.lang],
          id: item.id,
        };
      });

      this.allAreas = result;
      this.sectionAreas = [];
      this.section.areas.forEach((element) => {
        this.sectionAreas.push(element.id);
      });
      // this.section.areas = areas;
    },
    cancel() {
      this.sectionAreas = [];
      this.section = {
        name: {},
        description: {},
      };
      this.dialog = false;
    },
    updateSection: async function () {
      if (this.$refs.form.validate()) {
        this.progressEditSection = true;
        var state = await sectionController.updateSection(
          this.file,
          this.section,
          this.sectionAreas
        );
        if (state === true) {
          this.dialog = false;
          this.$emit("getVal");

          // this.restData();
        }
      }
      this.progressEditSection = false;
    },
    changNation(nation) {
      this.section.nation_id = nation;
    },

    onFileChange(e) {
      if (e != null) {
        const reader = new FileReader();
        reader.readAsDataURL(this.file);
        reader.onload = (e) => {
          this.image = e.target.result;
        };
      } else {
        this.file = null;
        this.image = null;
      }
    },

    remove(item) {
      const index = this.sectionAreas.indexOf(item.id);
      if (index >= 0) this.sectionAreas.splice(index, 1);
    },
    restData() {
      this.image = null;
      this.$refs.form.reseft();
      this.file = null;
      this.sectionAreas = [];
      this.section = {
        name: {},
        description: {},
      };
      this.dialog = false;
      this.progressEditSection = false;
    },
  },
  computed: {
    lang() {
      return this.$store.getters.lang;
    },
  },
  components: {
    "build-nation-field": () => import("../nation.vue"),
  },
};
</script>
